.action-container,
.content-container {
  overflow: auto;
  height: 300px;
  min-height: 0;
}

.swiper-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.swiper-slide {
  color: #aaa;
  background-color: #eee;
  overflow: hidden !important;
  filter: hue-rotate(265deg);
}

.swiper-slide-active {
  filter: drop-shadow(2px 4px 6px black);
}

.slide-img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  &-wrap {
    position: relative;
  }
}
